import React from "react"
import Layout from "../../components/Layout"
import { graphql, Link } from "gatsby"

export default function Projects({ data }) {
  console.log(data)
  const projects = data.allMarkdownRemark.nodes
  
  return (
    <div>
      <Layout>
        <div className="container mx-auto px-4 md:px-20 pt-28 pb-24">
          <div className="text-5xl font-bold">THIS IS PROJECTS PAGE</div>
          <div className="text-3xl">You can find my personal projects here</div>
          <div className="pt-16 grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-8">
            {projects.map(project => (
			  <Link to={"/projects/" + project.frontmatter.slug} key={ project.id }>
			    <div className="duration-300 rounded-xl shadow-md items-center hover:shadow-xl">
			      <div className="">
					<img src="https://source.unsplash.com/random/320x180" alt="C/O https://placeholder.com/" className="rounded-t-xl w-full h-auto" />
				  </div>
			      <div className="p-4">
					<div className="text-2xl font-semibold hover:text-red-500">{ project.frontmatter.title }</div>
					<div className="text-lg">{ project.frontmatter.stack }</div>
			      </div>
			    </div>
			  </Link>
			))}
          </div>
        </div>
      </Layout>
    </div>
  )
}

//export page query
export const query = graphql`
query ProjectPage {
  allMarkdownRemark(
    filter: {fileAbsolutePath: {glob: "**/src/projects/*.md"}}
  ) {
    nodes {
      frontmatter {
        slug
        stack
        title
      }
      id
    }
  }
}
`
